import React from 'react';
import Media from 'react-media';
import style from './fourthScreen.module.css';
import debt from '../../../img/infoImg/debt.svg';
import list from '../../../img/infoImg/list.svg';
import debtorsRegister from '../../../img/infoImg/debtorsRegister.svg';

const FourthScreen = () => (
  <div className={style.wrapper}>
    <h2 className={style.header}>Автоматические списки должников</h2>
    <p className={style.text}>
      Стимулируйте прилежание Ваших абонентов с помощью списков должников и
      списков на отключение услуг. Данные формируются и отображаются как
      автоматически так и вручную
    </p>
    <div className={style.imgWrapper}>
      <img className={style.smallImg} src={debt} alt="debt icon" />
      <img className={style.smallImg} src={list} alt="list icon" />
    </div>
    <Media query={{ maxWidth: 1199 }}>
      {matches =>
        matches && (
          <img
            src={debtorsRegister}
            className={style.debtorsRegister}
            alt="debtorsRegister"
          />
        )
      }
    </Media>
  </div>
);

export default FourthScreen;
